import { useCallback } from 'react';

import {
  createCollaborationRequest,
  createCollaborationRequestNotification,
  submitFinalComment,
  getReviewState,
} from '../../../services';
import { getCurrentUser } from '../../../utils/currentUser';

/**
 * useReviewActions Hook - 
 * @param {Object} params - 
 * @param {Object} params.state - 
 * @param {Object} params.setters - 
 * @param {string} params.projectId - ID
 * @param {string} params.userId - ID
 * @param {string} params.userName - 
 * @param {string} params.projectName - 
 * @param {Function} params.onSendRequest - 
 * @param {Function} params.onSubmitComment - 
 * @returns {Object} 
 */
const useReviewActions = ({
  state,
  setters,
  projectId,
  userId,
  userName,
  userRole,
  projectName,
  onSendRequest = () => {
    // TODO: Implement send request
  },
  onSubmitComment = () => {
    // TODO: Implement submit comment
  },
}) => {
  const {
    setLocalRequestStatus,
    setIsLoading,
    setError,
    setHasSubmittedLocal,
    setCommentText,
  } = setters;
  const { commentText } = state;

  // 
  const handleSendRequest = useCallback(async () => {
    setIsLoading(true);
    setError(null);

    try {
      const currentUser = getCurrentUser();

      // 
      const collaborationRequest = await createCollaborationRequest({
        projectId,
        projectName,
        requesterId: userId,
        requesterName: userName,
        requesterAvatar: currentUser?.avatar || '',
        ownerId: 'alice',
        ownerName: 'Alice Chen',
        message: 'I would like to join this collaboration project.',
      });

      // 
      await createCollaborationRequestNotification({
        userId: 'alice',
        projectId,
        projectName,
        requestId: collaborationRequest.id,
        requesterId: userId,
        requesterName: userName,
        requesterAvatar: currentUser?.avatar || '',
      });

      // 
      setLocalRequestStatus('pending');
      onSendRequest();
      return collaborationRequest; // 
    } catch (error) {
      setError(error.message);
      console.error('Error sending collaboration request:', error);
      throw error; // 
    } finally {
      setIsLoading(false);
    }
  }, [
    projectId,
    userId,
    userName,
    projectName,
    onSendRequest,
    setLocalRequestStatus,
    setIsLoading,
    setError,
  ]);

  // 
  const handleSubmitFinalComment = useCallback(async () => {
    setIsLoading(true);
    setError(null);

    try {
      const comment = await submitFinalComment(
        projectId,
        userId,
        commentText,
        'positive',
        'alice',
        projectName,
        userName,
        userRole
      );
      if (comment) {
        setHasSubmittedLocal(true);
        setCommentText('');
        onSubmitComment(commentText);
        return comment; // 
      }
      throw new Error('Failed to create comment');
    } catch (error) {
      setError(error.message);
      console.error('Error submitting final comment:', error);
      throw error; // 
    } finally {
      setIsLoading(false);
    }
  }, [
    projectId,
    userId,
    commentText,
    onSubmitComment,
    setHasSubmittedLocal,
    setCommentText,
    setIsLoading,
    setError,
    projectName,
    userName,
    userRole,
  ]);

  // 
  const handleRefreshStatus = useCallback(async () => {
    try {
      const state = await getReviewState(projectId, userId);
      setLocalRequestStatus(state);
    } catch (error) {
      console.error('Error refreshing review status:', error);
      setError('Failed to refresh status');
    }
  }, [projectId, userId, setLocalRequestStatus, setError]);

  // 
  const handleResetRequests = useCallback(async () => {
    try {
      const { resetReviewRequests } = await import('../../../services');
      await resetReviewRequests();
      setLocalRequestStatus('none');
    } catch (error) {
      console.error('Error resetting review requests:', error);
      setError('Failed to reset requests');
    }
  }, [setLocalRequestStatus, setError]);

  // 
  const handleCommentTextChange = useCallback(
    text => {
      setCommentText(text);
    },
    [setCommentText]
  );

  // 
  const handleClearError = useCallback(() => {
    setError(null);
  }, [setError]);

  return {
    handleSendRequest,
    handleSubmitFinalComment,
    handleRefreshStatus,
    handleResetRequests,
    handleCommentTextChange,
    handleClearError,
  };
};

export default useReviewActions;
