import React, { createContext, useContext, useState, useEffect } from 'react';

import { getCurrentUser } from '../utils/currentUser';

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  // 
  useEffect(() => {
    const loadUser = async () => {
      try {
        //  currentUser.js 
        const currentUser = getCurrentUser();
        setUser(currentUser);
      } catch (error) {
        console.error('[AuthContext] Error loading user:', error);
        setUser(null);
      } finally {
        setLoading(false);
      }
    };

    // 
    loadUser();

    // loading
    const timeoutId = setTimeout(() => {
      setLoading(false);
    }, 3000);

    return () => clearTimeout(timeoutId);
  }, []);

  // 
  useEffect(() => {
    const handleUserChange = () => {
      try {
        const currentUser = getCurrentUser();
        setUser(currentUser);
      } catch (error) {
        console.error('Error handling user change:', error);
        setUser(null);
      }
    };

    window.addEventListener('user:changed', handleUserChange);
    return () => window.removeEventListener('user:changed', handleUserChange);
  }, []);

  // 
  const login = async (email, password) => {
    try {
      // 
      // const { data, error } = await supabase.auth.signInWithPassword({
      //   email,
      //   password
      // });

      //  currentUser.js 
      const currentUser = getCurrentUser();
      setUser(currentUser);

      return { success: true };
    } catch (error) {
      console.error('Login error:', error);
      return { success: false, error: error.message };
    }
  };

  // 
  const logout = () => {
    setUser(null);
    localStorage.removeItem('tag_user');
    sessionStorage.removeItem('tag_user');
    //  currentUser 
    localStorage.removeItem('tag.currentUserId');
  };

  // 
  const isAuthenticated = () => {
    return user !== null;
  };

  const value = {
    user,
    loading,
    login,
    logout,
    isAuthenticated,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};
